table{
  width: 80%;
  text-transform: capitalize;
}
div{
  margin-left: 15rem;
  width: 80%;
  padding-top: "1rem";
}

.title{
  
  width: 80%;
  text-transform: uppercase;
  background-image: -webkit-linear-gradient(red, yellow);
  text-align: center;
  font-family:'Times New Roman', Times, serif
}
th{
  text-align: left;
  font-size: x-large;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-style: italic;

}
input{
  background-color: lightgoldenrodyellow;
  color: black;
  padding: 0.5 rem;
  width: 60%;
  font-size: large;
  font-family: 'Courier New', Courier, monospace;
}


*{
  box-sizing: border-box;
}

.search-box{
  width: fit-content;
  height: fit-content;
  position: relative;
}
.input-search{
  height: 50px;
  width: 50px;
  border-style: none;
  padding: 10px;
  font-size: 18px;
  letter-spacing: 2px;
  outline: none;
  border-radius: 25px;
  transition: all .5s ease-in-out;
  background-color: lightblue;
  padding-right: 40px;
  color:black;
}
.input-search::placeholder{
  color:blue;
  font-size: 18px;
  letter-spacing: 2px;
  font-weight: 100;
}
.btn-search{
  width: 50px;
  height: 50px;
  border-style: none;
  font-size: 20px;
  font-weight: bold;
  outline: none;
  cursor: pointer;
  border-radius: 50%;
  position: absolute;
  right: 0px;
  color:black ;
  background-color:transparent;
  pointer-events: painted;  
}
.btn-search:focus ~ .input-search{
  width: 300px;
  border-radius: 0px;
  background-color: transparent;
  border-bottom:1px solid rgba(255,255,255,.5);
  transition: all 500ms cubic-bezier(0, 0.110, 0.35, 2);
}
.input-search:focus{
  width: 300px;
  border-radius: 0px;
  background-color: transparent;
  border-bottom:1px solid rgba(255, 0, 0, 0.5);
  transition: all 500ms cubic-bezier(0, 0.110, 0.35, 2);
}
